import { prefix } from '../../globals/settings';
import { html, LitElement } from 'lit-element';
import { property } from 'lit/decorators.js';
import { carbonElement as customElement } from '../../globals/decorators/carbon-element';
import styles from './footer.scss';

/**
 * Section Title.
 *
 * @element cds-footer
 */

@customElement(`${prefix}-footer`)
export class CDSFooter extends LitElement {
  @property({ type: String }) companyName = 'Valsts Kase';
  @property({ type: String }) copyrightText = '© kase.gov.lv';
  @property({ type: Number }) copyrightYear = 2023;
  @property({ type: String }) allRightsResserved = 'Visas tiesības aizsargātas';
  @property({ type: String }) servicePortal = 'Pakalpojumu portāls';
  @property({ type: String }) phoneNumberSectionText =
    'Atbalsta dienesta tālrunis';
  @property({ type: String }) phoneNumber = '67094368';
  @property({ type: String }) emailSectionText = 'e-pasts';
  @property({ type: String }) email = 'ekase@kase.gov.lv';
  @property({ type: String }) workTimeSectionText = 'Darba laiks';
  @property({ type: String }) workTime = 'P - Pk. 08.30 - 17.00';

  static styles = styles;
  render() {
    const {
      allRightsResserved,
      companyName,
      copyrightText,
      copyrightYear,
      servicePortal,
      phoneNumberSectionText,
      phoneNumber,
      emailSectionText,
      email,
      workTimeSectionText,
      workTime,
    } = this;

    return html` <link
        rel="stylesheet"
        href="https://demo.turn.lv/cfla_dist/assets/css/turn-carbon-grid.css" />
      <div class="footer cds--css-grid cds--css-grid--full-width">
        <div
          class="footer--mobile-title cds--css-grid-column cds--sm:col-span-4">
          ${companyName}
        </div>
        <div
          class="footer__section cds--css-grid-column cds--sm:col-span-4 cds--md:col-span-8 cds--lg:col-span-4">
          ${servicePortal}
        </div>
        <div
          class=" footer__section footer__section--divider cds--css-grid-column cds--sm:col-span-4 cds--md:col-span-8 cds--lg:col-span-4">
          ${phoneNumberSectionText}: <span>${phoneNumber}</span>
        </div>
        <div
          class=" footer__section footer__section--divider cds--css-grid-column cds--sm:col-span-4 cds--md:col-span-8 cds--lg:col-span-4">
          ${emailSectionText}: <span>${email}</span>
        </div>
        <div
          class=" footer__section footer__section--divider cds--css-grid-column cds--sm:col-span-4 cds--md:col-span-8 cds--lg:col-span-4">
          ${workTimeSectionText}: <span>${workTime}</span>
        </div>
        <div
          class="footer--mobile-info cds--css-grid-column cds--sm:col-span-4">
          ${copyrightText}, ${copyrightYear}.
          <span>${allRightsResserved}</span>
        </div>
      </div>`;
  }
}
